@import url(https://fonts.googleapis.com/css2?family=Oxygen&display=swap);
body{height:100vh;width:100vw;margin:0;padding:0;background-color:#24252A;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.loader{margin-top:15%;border:16px solid #28292f;border-top:16px solid teal;border-radius:50%;width:120px;height:120px;-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite}@-webkit-keyframes spin{0%{-webkit-transform:rotate(0deg) scale(1);transform:rotate(0deg) scale(1)}50%{-webkit-transform:rotate(180deg) scale(1.5);transform:rotate(180deg) scale(1.5)}100%{-webkit-transform:rotate(360deg) scale(1);transform:rotate(360deg) scale(1)}}@keyframes spin{0%{-webkit-transform:rotate(0deg) scale(1);transform:rotate(0deg) scale(1)}50%{-webkit-transform:rotate(180deg) scale(1.5);transform:rotate(180deg) scale(1.5)}100%{-webkit-transform:rotate(360deg) scale(1);transform:rotate(360deg) scale(1)}}

.App{background-color:#3B3C42;margin:0;padding:0;text-align:center;font-family:'Oxygen', sans-serif}

